<template>
  <div class="release">
    <div class="top">
      <div class="loadTit">
        <span>上传照片</span><a>最多可上传10张图片</a>
      </div>
      <el-upload style="clear:both"
        action="http://api.love.anheqiaobei.com/api/wechat/img"
        list-type="picture-card" name='image'
        :on-preview="handlePictureCardPreview"
        :on-remove="handleRemove1"
        :on-success	="fileChange1">
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible" size="tiny">
        <img width="100%" :src="dialogImageUrl" alt />
      </el-dialog>
      <div class="loadTit">
        <span>上传微信二维码</span>
      </div>
      <el-upload style="clear:both"
        action="http://api.love.anheqiaobei.com/api/wechat/img"
        list-type="picture-card" name='image'
        :on-preview="handlePictureCardPreview"
        :on-remove="handleRemove2"
        :on-success	="fileChange2">
        <i class="el-icon-plus"></i>
      </el-upload>
      <div class="loadTit">
        <span>基本资料</span>
      </div>
      <div>
        <!-- <mt-field label="用户名" placeholder="请输入用户名" v-model="userName"></mt-field> -->
        <mt-field class="chan" label="性别" placeholder="请输入性别">
          <div @click="aa" class="ipt">{{sexMsg}}</div>
        </mt-field>
        <mt-field label="籍贯" placeholder="请选择籍贯" type="tel"  v-model="address"></mt-field>
        <mt-field label="出生日期" placeholder="请选择出生日期" type="date" v-model="dateVal"></mt-field>
        <mt-field label="身高" placeholder="请输入身高（cm）" type="number" v-model="height"></mt-field>
        <mt-field label="体重" placeholder="请输入体重（kg）" type="number" v-model="weight"></mt-field>
        <mt-field label="自我介绍" placeholder="自我介绍（200字以内）" type="textarea" rows="3" v-model="self_intro"></mt-field>
        <mt-field label="择偶标准" placeholder="择偶标准（200字以内）" type="textarea" rows="3" v-model="friend_condition"></mt-field>
        <mt-button style="margin-top:0.3rem" type="primary" size="large" @click="sub">发 布</mt-button>
      </div>
    </div>
    <div>
    </div>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import {release} from '@/request/api.js'
export default {
  name: "release",
  data() {
    return {
      imgList1:[],
      imgList2:[],
      dialogImageUrl: "",
      dialogVisible: false,
      sexBol:false,
      sex:'',
      sexMsg:'请选择性别',
      dateVal:'',
      self_intro:'',
      friend_condition:'',
      address:'',
      weight:'',
      height:'',
    };
  },
  methods: {
    sub(){
      release({
        self_img:this.imgList1,
        wechat_img:this.imgList2,
        gender:'1',
        address:'河南省周口市',
        age:'123',
        height:this.height,
        weight:this.weight,
        self_intro:this.self_intro,
        friend_condition:this.friend_condition
      }).then(res=>{
        console.log(res);
      })
    },
    aa(){
      this.sexBol=true;
    },
    clickAction(val){
      alert(val)
    },
    // 照片上传成功
    fileChange1(file, fileList){
        this.imgList1.push(fileList.response.data.img_url);
    },
    // 照片删除
    handleRemove1(file, fileList) {
      this.imgList1=[];
      for(var i in fileList){
        // console.log(fileList[i].response.data.img_url);
        this.imgList1.push(fileList[i].response.data.img_url);
      }
    },
    // 二维码上传成功
    fileChange2(file, fileList){
      this.imgList2.push(fileList.response.data.img_url);
    },
    // 二维码删除
    handleRemove2(file, fileList) {
      this.imgList2=[];
      for(var i in fileList){
        // console.log(fileList[i].response.data.img_url);
        this.imgList2.push(fileList[i].response.data.img_url);
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 解析base64格式
    // files(e) {
    //   console.log(e.target.files[0]);
    //   let reader = new FileReader();
    //   reader.readAsDataURL(e.target.files[0]);
    //   reader.onloadend = function() {
    //     let result = this.result;
    //     console.log(this.result);
    //   };
    // }
  },
  mounted() {
     
  }
};
</script>
<style  scoped>
.release{
  margin-bottom:1.5rem 
}
.top {
  font-size: 0.26rem;
}
.top .loadTit{
  height: 0.8rem;
  line-height: 0.8rem
}
.loadTit>span{
  float: left;
}
.loadTit>a{
  float: right;
}
.top /deep/ .el-upload-list {
  width: 100%;
}
.top /deep/ .el-upload-list__item {
  width: 1.5rem;
  height: 1.5rem;
}
.top /deep/ .el-upload {
  width: 1.5rem;
  height: 1.5rem;
  line-height: 1.5rem;
}
.chan /deep/ .mint-field-other{
  width: 100%;
  height: 100%;
}
.ipt{
  height: 100%;
    appearance: none;
    flex: 1;
    outline: 0;
    line-height: 1.6;
    font-size: inherit;
    width: 100%;
}
</style>
