<template>
  <div class="about">
    <div class="top">
      <span :class="active==1?'activeClass':''" @click="tab(1)">最新</span><span :class="active==2?'activeClass':''" @click="tab(2)">最热</span>
    </div>
    <div class="cont">
      <ul>
        <li v-for="(item,i) in list" :key="i">
          <img :src="item['self_img'][0]" alt="">
          <div><p class="username">善良的毛豆</p><p>{{item.age}}|{{item.height}}|{{item.weight}}</p></div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
import {fetchList,getList} from '@/request/api.js'
export default {
  name: 'about',
  data(){
    return{
      loading:false,
      active:1,
      selected:'/home',
      list:[1,2,3,4,5,6],
      allLoaded:false
    }
  },
  watch:{
    selected(newVal){
      this.$router.push(newVal)
    }
  },
  methods:{
    tab(i){
      this.active=i;
    },
    loadMore(){
      this.loading = true;
      setTimeout(() => {
        this.list=this.list.concat([1,2,3]);
        this.loading = false;
      }, 1500);
    }
  },
  mounted(){
    location.href='http://api.love.anheqiaobei.com/api/wechat/auth?target_url=https://www.baidu.com/';
    getList({limit:'20'}).then(res=>{
      console.log(res.data.data);
      this.list=res.data.data;
    })
  }
}
</script>
